@font-face {
  font-family: 'Epilogue_Title';
  src: url(/my-test-project/static/media/Epilogue-Medium.ef359a4e.ttf);
}
@font-face {
  font-family: 'Epilogue_Sub';
  src: url(/my-test-project/static/media/Epilogue-Black.bb7e1465.ttf);
}
body {
  margin: 0;
  padding: 0;
  background-color: rgb(14, 12, 32);
}
* {
  font-family: 'Epilogue_Title';
  font-size: 18px;
  color:rgb(213, 213, 213);
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#header {
    width: 100%;
    height: 100px;
    display: flex;
    flex-direction: row;
    align-items: center;
}
#logo {
    width: 30%;
    font-size: 30px;
    font-family: 'Epilogue_Title';
}
#inforHeader {
    width: 65%;
    display: flex;
    justify-content: flex-end;
    grid-gap: 20px;
    gap: 20px;
}
#inforHeader > a {
    font-family: 'Epilogue_Title';
    font-size: 16px;
}

#iconMenu {
    position: absolute;
    top: 3%;
    right: 2%;
    width: 2%;
    text-align: center;
    position: fixed;
    z-index: 5;
    cursor: pointer;
    
}
#iconMenu.active {
    display: none;
}
.menu {
    position: absolute;
    top: 80px;
    right: 20%;
    height: 40vh;
    text-align: right;
    display: none;
}
#contentMenu {
    padding-bottom: 20px;
}
#contentMenu:hover {
    color: rgb(254, 118, 45);
}
#menuBar.active {
    display: block;
}
#iconClose {
    display: none;
}
#iconOpen.active {
    display: none;
}

#iconClose.active {
    display: block;
}
#backgroundMenu {
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(14, 12, 32, 0.451);
    z-index: 0;
    -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px);
    position: fixed;
    display: none;
    transition: 0.5s ease-in-out;
}
#backgroundMenu.active {
    display: block;
    z-index: 4;
}


