    height: 75px;
}
#box:nth-child(3) > #square1 {
    top: 30px;
    left: 30px;
    background: transparent;
    border: 1px white solid;
    z-index: 2;
}
#box:nth-child(3) > #square2 {
    left: 30px;
    top: 30px;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    background-color: rgb(255, 64, 96);
    z-index: 1;
}
